import React from "react";
import OverallData from "./OverallData";
import "../App.css";

const OverallStats = () => {
  const mainStyle = {
    backgroundSize: "cover",
    color: "white",
    padding: "20px",
    height: "1440px",
    display: "flex",
    flexDirection: "column",
    alignItems: "center",
  };

  return (
    <div style={mainStyle}>
      <OverallData username="yxn" apiKey={process.env.REACT_APP_OSU_API_KEY} />
    </div>
  );
};

export default OverallStats;
