import React from "react";
import {
  LineChart,
  Line,
  XAxis,
  YAxis,
  CartesianGrid,
  Tooltip,
  Legend,
} from "recharts";
import { Box } from "@mui/material";

const data = [
  { name: "Jan", Catch_SS_Ranks: 6329 },
  { name: "Feb", Catch_SS_Ranks: 6329 },
  { name: "Mar", Catch_SS_Ranks: 6329 },
  { name: "Apr", Catch_SS_Ranks: 6514 },
  { name: "May", Catch_SS_Ranks: 6670 },
  { name: "Jun", Catch_SS_Ranks: 6755 },
  { name: "Jul", Catch_SS_Ranks: 6959 },
  { name: "Aug", Catch_SS_Ranks: 7298 },
  { name: "Sep", Catch_SS_Ranks: 7298 },
  { name: "Oct", Catch_SS_Ranks: 7298 },
  { name: "Nov", Catch_SS_Ranks: 7298 },
  { name: "Dec", Catch_SS_Ranks: 7298 },
];

const CatchLineGraph2 = () => {
  const CustomTooltip = ({ active, payload, label }) => {
    if (active && payload && payload.length) {
      const Catch_SS_Ranks = payload[0].value;
      return (
        <div
          className="custom-tooltip"
          style={{
            backgroundColor: "black",
            color: "white",
            padding: "1px",
            width: "100px",
            border: "1px solid gray",
            transition: "background-color 0.3s",
          }}
        >
          <p className="label">{`${label}: ${Catch_SS_Ranks}`}</p>
        </div>
      );
    }
    return null;
  };

  const minYValue =
    Math.floor(Math.min(...data.map((item) => item.Catch_SS_Ranks)) / 10) * 10 -
    10;
  const maxYValue =
    Math.ceil(Math.max(...data.map((item) => item.Catch_SS_Ranks)) / 10) * 10;

  return (
    <Box
      display="flex"
      justifyContent="center"
      alignItems="center"
      flexDirection="column"
      marginTop={2}
      marginRight={1}
      padding={2}
      border={2}
      borderColor="gray"
      width="98%"
      height="37%"
    >
      <Box width="100%">
        <LineChart
          data={data}
          width={540}
          height={300}
          margin={{ top: 20, right: 40, left: 0, bottom: 20 }}
        >
          <CartesianGrid strokeDasharray="3 3" />
          <XAxis dataKey="name" interval={0} tickCount={data.length} />
          <YAxis domain={[minYValue, maxYValue + 100]} />
          <Tooltip content={<CustomTooltip />} />
          <Legend />
          <Line
            type="monotone"
            dataKey="Catch_SS_Ranks"
            stroke="cyan"
            activeDot={{ r: 8 }}
          />
        </LineChart>
      </Box>
    </Box>
  );
};

export default CatchLineGraph2;
