import React from "react";
import {
  LineChart,
  Line,
  XAxis,
  YAxis,
  CartesianGrid,
  Tooltip,
  Legend,
} from "recharts";
import { Box } from "@mui/material";

const data = [
  { name: "Jan", Taiko_Ranked_Score: 22.3 },
  { name: "Feb", Taiko_Ranked_Score: 22.3 },
  { name: "Mar", Taiko_Ranked_Score: 22.3 },
  { name: "Apr", Taiko_Ranked_Score: 22.3 },
  { name: "May", Taiko_Ranked_Score: 22.3 },
  { name: "Jun", Taiko_Ranked_Score: 22.3 },
  { name: "Jul", Taiko_Ranked_Score: 25.6 },
  { name: "Aug", Taiko_Ranked_Score: 28.0 },
  { name: "Sep", Taiko_Ranked_Score: 28.0 },
  { name: "Oct", Taiko_Ranked_Score: 28.0 },
  { name: "Nov", Taiko_Ranked_Score: 28.0 },
  { name: "Dec", Taiko_Ranked_Score: 28.0 },
];

const formatTaiko_Ranked_Score = (Taiko_Ranked_Score) => {
  return `${Taiko_Ranked_Score}M`;
};

const TaikoLineGraph = () => {
  const CustomTooltip = ({ active, payload, label }) => {
    if (active && payload && payload.length) {
      const Taiko_Ranked_Score = payload[0].value;
      return (
        <div
          className="custom-tooltip"
          style={{
            backgroundColor: "black",
            color: "white",
            padding: "1px",
            width: "100px",
            border: "1px solid gray",
            transition: "background-color 0.3s",
          }}
        >
          <p className="label">{`${label}: ${formatTaiko_Ranked_Score(
            Taiko_Ranked_Score,
          )}`}</p>
        </div>
      );
    }
    return null;
  };

  const minYValue = Math.min(...data.map((item) => item.Taiko_Ranked_Score));
  const maxYValue =
    Math.ceil(Math.max(...data.map((item) => item.Taiko_Ranked_Score)) / 10) *
    10;

  return (
    <Box
      display="flex"
      justifyContent="center"
      alignItems="center"
      flexDirection="column"
      marginTop={2}
      marginRight={1}
      padding={2}
      border={2}
      borderColor="gray"
      width="98%"
      height="37%"
    >
      <Box width="100%">
        <LineChart
          data={data}
          width={540}
          height={300}
          margin={{ top: 20, right: 40, left: 0, bottom: 20 }}
        >
          <CartesianGrid strokeDasharray="3 3" />
          <XAxis dataKey="name" interval={0} tickCount={data.length} />
          <YAxis
            tickFormatter={formatTaiko_Ranked_Score}
            domain={[minYValue, maxYValue]}
          />
          <Tooltip content={<CustomTooltip />} />
          <Legend />
          <Line
            type="monotone"
            dataKey="Taiko_Ranked_Score"
            stroke="cyan"
            activeDot={{ r: 8 }}
          />
        </LineChart>
      </Box>
    </Box>
  );
};

export default TaikoLineGraph;
