import React, { useEffect, useState } from "react";
import {
  Paper,
  Table,
  TableBody,
  TableCell,
  TableContainer,
  TableHead,
  TableRow,
} from "@mui/material";
import TaikoLineGraph from "./TaikoLineGraph";
import TaikoLineGraph2 from "./TaikoLineGraph2";
import TaikoImage from "../images/Taiko.png";
import "../App.css";

const TaikoData = ({ username, apiKey }) => {
  const [userData, setUserData] = useState(null);
  const [taikoSSRanks, setTaikoSSRanks] = useState(0);
  const [taikoPP, setTaikoPP] = useState(0);
  const [taikoRankedScore, setTaikoRankedScore] = useState(0);
  const [taikoPlaytime, setTaikoPlaytime] = useState(0);
  const [isLoading, setIsLoading] = useState(true);
  const formatNumber = (number) => {
    if (number >= 1000000000000) {
      const trillions = (number / 1000000000000).toFixed(2);
      return `${trillions}T`;
    } else if (number >= 1000000000) {
      const billions = (number / 1000000000).toFixed(2);
      return `${billions}B`;
    } else if (number >= 1000000) {
      const millions = (number / 1000000).toFixed(2);
      return `${millions}M`;
    }
    return number.toLocaleString();
  };

  useEffect(() => {
    const fetchUserData = async () => {
      try {
        const response = await fetch(
          `https://osu.ppy.sh/api/get_user?k=${apiKey}&u=${username}&type=string`,
        );
        const data = await response.json();
        setUserData(data[0]);
        setIsLoading(false);
      } catch (error) {
        console.error("Error fetching user data:", error);
        setIsLoading(false);
      }
    };

    const fetchPlaytime = async (mode) => {
      try {
        const response = await fetch(
          `https://osu.ppy.sh/api/get_user?k=${apiKey}&u=${username}&type=string&m=${mode}`,
        );
        const userData = await response.json();
        if (userData.length > 0) {
          const playtimeInSeconds = parseFloat(
            userData[0].total_seconds_played,
          );
          let playtimeInHours = (playtimeInSeconds / 3600).toFixed(2);
          if (mode === 1) {
            playtimeInHours = parseFloat(playtimeInHours);
            setTaikoPlaytime(playtimeInHours);
          }
        }
      } catch (error) {
        console.error(`Error fetching playtime for mode ${mode}:`, error);
      }
    };

    const fetchSSRanksAndPP = async (mode) => {
      try {
        const response = await fetch(
          `https://osu.ppy.sh/api/get_user?k=${apiKey}&u=${username}&type=string&m=${mode}`,
        );
        const userData = await response.json();
        const ssRanks = parseInt(userData[0].count_rank_ss || 0);
        const sshRanks = parseInt(userData[0].count_rank_ssh || 0);
        const rankedScore = parseInt(userData[0].ranked_score || 0);
        if (mode === 1) {
          setTaikoSSRanks(ssRanks + sshRanks);
          setTaikoPP(userData[0].pp_raw || 0);
          setTaikoRankedScore(rankedScore);
        }
      } catch (error) {
        console.error(`Error fetching user SS ranks in mode ${mode}:`, error);
      }
    };

    fetchUserData();
    fetchSSRanksAndPP(1);
    fetchPlaytime(1);
  }, [username, apiKey]);

  if (isLoading) {
    return <div style={{ marginTop: "20px" }}>Loading...</div>;
  }

  if (!userData) {
    return <div>User not found</div>;
  }

  return (
    <div>
      <div style={{ display: "flex", marginBottom: "20px" }}>
        <div style={{ flex: 1 }}>
          <TableContainer
            component={Paper}
            style={{
              backgroundColor: "rgba(9, 9, 9, 0.7)",
              border: "2px solid gray",
            }}
          >
            <Table>
              <TableHead>
                <TableRow>
                  <TableCell
                    style={{
                      color: "white",
                      fontSize: "26px",
                      padding: "8px",
                      textAlign: "center",
                    }}
                  >
                    Mode
                  </TableCell>
                  <TableCell
                    style={{
                      color: "white",
                      fontSize: "26px",
                      padding: "8px",
                      textAlign: "center",
                    }}
                  >
                    Score
                  </TableCell>
                  <TableCell
                    style={{
                      color: "white",
                      fontSize: "26px",
                      padding: "8px",
                      textAlign: "center",
                    }}
                  >
                    SS
                  </TableCell>
                  <TableCell
                    style={{
                      color: "white",
                      fontSize: "26px",
                      padding: "8px",
                      textAlign: "center",
                    }}
                  >
                    PP
                  </TableCell>
                </TableRow>
              </TableHead>
              <TableBody>
                <TableRow>
                  <TableCell style={{ paddingLeft: "40px" }}>
                    <div className="image-container">
                      <img
                        src={TaikoImage}
                        alt="Taiko"
                        width="70"
                        className="glow-on-hover"
                      />
                      <div
                        className="playtime-overlay"
                        style={{ fontSize: "18px" }}
                      >
                        {parseInt(taikoPlaytime)} Hours
                      </div>
                    </div>
                  </TableCell>
                  <TableCell
                    style={{
                      color: "white",
                      fontSize: "30px",
                      paddingLeft: "40px",
                    }}
                  >
                    {formatNumber(taikoRankedScore)}
                  </TableCell>
                  <TableCell
                    style={{
                      color: "white",
                      fontSize: "30px",
                      paddingLeft: "40px",
                    }}
                  >
                    {taikoSSRanks}
                  </TableCell>
                  <TableCell
                    style={{
                      color: "white",
                      fontSize: "30px",
                      paddingLeft: "40px",
                    }}
                  >
                    {parseInt(taikoPP)}
                  </TableCell>
                </TableRow>
              </TableBody>
            </Table>
          </TableContainer>
          <div style={{ flex: 1, marginRight: "20px" }}>
            <TaikoLineGraph />
            <TaikoLineGraph2 />
          </div>
        </div>
      </div>
    </div>
  );
};

export default TaikoData;
