import React from "react";
import "../App.css";

const SocialMedia = () => {
  const mainStyle = {
    backgroundSize: "cover",
    color: "white",
    padding: "20px",
    display: "flex",
    flexDirection: "column",
    alignItems: "center",
  };

  const logosContainerStyle = {
    marginTop: "40px",
    fontSize: "32px",
    width: "575px",
    display: "flex",
    justifyContent: "center",
    alignItems: "center",
    backgroundColor: "rgba(9, 9, 9, 0.7)",
    border: "2px solid gray",
    flexDirection: "row",
  };

  const imageStyle = {
    height: "80px",
    width: "80px",
    marginRight: "20px",
    marginTop: "7px",
  };

  return (
    <div style={mainStyle}>
      <div style={logosContainerStyle}>
        <a
          href="https://kick.com/y-x-n"
          target="_blank"
          rel="noopener noreferrer"
          style={{ color: "white" }}
        >
          <img
            src="https://cdn.discordapp.com/attachments/1123759840737636453/1139024020029849611/Kick-Favicon57x57.png"
            alt="Kick Logo"
            style={{
              ...imageStyle,
              filter: "grayscale(100%)",
              transition: "filter 0.3s ease",
            }}
            className="glow-on-hover"
            onMouseOver={(e) =>
              (e.currentTarget.style.filter = "grayscale(25%)")
            }
            onMouseOut={(e) =>
              (e.currentTarget.style.filter = "grayscale(100%)")
            }
          />
        </a>
        <a
          href="https://myanimelist.net/profile/yxn"
          target="_blank"
          rel="noopener noreferrer"
          style={{ color: "white" }}
        >
          <img
            src="https://cdn.discordapp.com/attachments/1123759840737636453/1139024020268908655/mal-favicon.png"
            alt="MyAnimeList Logo"
            style={{
              ...imageStyle,
              filter: "grayscale(100%)",
              transition: "filter 0.3s ease",
            }}
            className="glow-on-hover"
            onMouseOver={(e) =>
              (e.currentTarget.style.filter = "grayscale(25%)")
            }
            onMouseOut={(e) =>
              (e.currentTarget.style.filter = "grayscale(100%)")
            }
          />
        </a>
        <a
          href="https://twitter.com/why_XN"
          target="_blank"
          rel="noopener noreferrer"
          style={{ color: "white" }}
        >
          <img
            src="https://cdn.discordapp.com/attachments/1123759840737636453/1140098364025872414/twitter_favicon.png"
            alt="Twitter Logo"
            style={{
              ...imageStyle,
              filter: "grayscale(100%)",
              transition: "filter 0.3s ease",
            }}
            className="glow-on-hover"
            onMouseOver={(e) =>
              (e.currentTarget.style.filter = "grayscale(25%)")
            }
            onMouseOut={(e) =>
              (e.currentTarget.style.filter = "grayscale(100%)")
            }
          />
        </a>
        <a
          href="https://www.youtube.com/channel/UCmpX7DKujV64SSRawepV6cw"
          target="_blank"
          rel="noopener noreferrer"
          style={{ color: "white" }}
        >
          <img
            src="https://cdn.discordapp.com/attachments/1123759840737636453/1139024021028077658/youtube_favicon.png"
            alt="YouTube Logo"
            style={{
              ...imageStyle,
              filter: "grayscale(100%)",
              transition: "filter 0.3s ease",
            }}
            className="glow-on-hover"
            onMouseOver={(e) =>
              (e.currentTarget.style.filter = "grayscale(25%)")
            }
            onMouseOut={(e) =>
              (e.currentTarget.style.filter = "grayscale(100%)")
            }
          />
        </a>
        <a
          href="https://osu.ppy.sh/users/4075694"
          target="_blank"
          rel="noopener noreferrer"
          style={{ color: "white" }}
        >
          <img
            src="https://cdn.discordapp.com/attachments/1123759840737636453/1139024020524781568/osu_favicon.png"
            alt="osu Logo"
            style={{
              ...imageStyle,
              filter: "grayscale(100%)",
              transition: "filter 0.3s ease",
            }}
            className="glow-on-hover"
            onMouseOver={(e) =>
              (e.currentTarget.style.filter = "grayscale(25%)")
            }
            onMouseOut={(e) =>
              (e.currentTarget.style.filter = "grayscale(100%)")
            }
          />
        </a>
      </div>
    </div>
  );
};

export default SocialMedia;
