import React from "react";
import {
  LineChart,
  Line,
  XAxis,
  YAxis,
  CartesianGrid,
  Tooltip,
  Legend,
} from "recharts";
import { Box } from "@mui/material";

const data = [
  { name: "Jan", Mania_Ranked_Score: 0.5 },
  { name: "Feb", Mania_Ranked_Score: 0.5 },
  { name: "Mar", Mania_Ranked_Score: 0.5 },
  { name: "Apr", Mania_Ranked_Score: 0.5 },
  { name: "May", Mania_Ranked_Score: 0.5 },
  { name: "Jun", Mania_Ranked_Score: 0.6 },
  { name: "Jul", Mania_Ranked_Score: 0.6 },
  { name: "Aug", Mania_Ranked_Score: 0.7 },
  { name: "Sep", Mania_Ranked_Score: 0.7 },
  { name: "Oct", Mania_Ranked_Score: 0.7 },
  { name: "Nov", Mania_Ranked_Score: 0.7 },
  { name: "Dec", Mania_Ranked_Score: 0.7 },
];

const formatMania_Ranked_Score = (Mania_Ranked_Score) => {
  return `${Mania_Ranked_Score}B`;
};

const ManiaLineGraph = () => {
  const CustomTooltip = ({ active, payload, label }) => {
    if (active && payload && payload.length) {
      const Mania_Ranked_Score = payload[0].value;
      return (
        <div
          className="custom-tooltip"
          style={{
            backgroundColor: "black",
            color: "white",
            padding: "1px",
            width: "100px",
            border: "1px solid gray",
            transition: "background-color 0.3s",
          }}
        >
          <p className="label">{`${label}: ${formatMania_Ranked_Score(
            Mania_Ranked_Score,
          )}`}</p>
        </div>
      );
    }
    return null;
  };

  const minYValue = Math.min(...data.map((item) => item.Mania_Ranked_Score));
  const maxYValue = Math.ceil(
    Math.max(...data.map((item) => item.Mania_Ranked_Score)),
  );

  return (
    <Box
      display="flex"
      justifyContent="center"
      alignItems="center"
      flexDirection="column"
      marginTop={2}
      marginRight={1}
      padding={2}
      border={2}
      borderColor="gray"
      width="98%"
      height="37%"
    >
      <Box width="100%">
        <LineChart
          data={data}
          width={540}
          height={300}
          margin={{ top: 20, right: 40, left: 0, bottom: 20 }}
        >
          <CartesianGrid strokeDasharray="3 3" />
          <XAxis dataKey="name" interval={0} tickCount={data.length} />
          <YAxis
            tickFormatter={formatMania_Ranked_Score}
            domain={[minYValue, maxYValue]}
          />
          <Tooltip content={<CustomTooltip />} />
          <Legend />
          <Line
            type="monotone"
            dataKey="Mania_Ranked_Score"
            stroke="cyan"
            activeDot={{ r: 8 }}
          />
        </LineChart>
      </Box>
    </Box>
  );
};

export default ManiaLineGraph;
