import React from "react";
import {
  LineChart,
  Line,
  XAxis,
  YAxis,
  CartesianGrid,
  Tooltip,
  Legend,
} from "recharts";
import { Box } from "@mui/material";

const data = [
  { name: "Jan", Overall_Ranked_Score: 64.4 },
  { name: "Feb", Overall_Ranked_Score: 64.4 },
  { name: "Mar", Overall_Ranked_Score: 67.2 },
  { name: "Apr", Overall_Ranked_Score: 70.4 },
  { name: "May", Overall_Ranked_Score: 73.9 },
  { name: "Jun", Overall_Ranked_Score: 76.8 },
  { name: "Jul", Overall_Ranked_Score: 83.2 },
  { name: "Aug", Overall_Ranked_Score: 90.9 },
  { name: "Sep", Overall_Ranked_Score: 90.9 },
  { name: "Oct", Overall_Ranked_Score: 90.9 },
  { name: "Nov", Overall_Ranked_Score: 90.9 },
  { name: "Dec", Overall_Ranked_Score: 90.9 },
];

const formatOverall_Ranked_Score = (Overall_Ranked_Score) => {
  return `${Overall_Ranked_Score}B`;
};

const OsuLineGraph = () => {
  const CustomTooltip = ({ active, payload, label }) => {
    if (active && payload && payload.length) {
      const Overall_Ranked_Score = payload[0].value;
      return (
        <div
          className="custom-tooltip"
          style={{
            backgroundColor: "black",
            color: "white",
            padding: "1px",
            width: "100px",
            border: "1px solid gray",
            transition: "background-color 0.3s",
          }}
        >
          <p className="label">{`${label}: ${formatOverall_Ranked_Score(
            Overall_Ranked_Score,
          )}`}</p>
        </div>
      );
    }
    return null;
  };

  const minYValue = Math.min(...data.map((item) => item.Overall_Ranked_Score));
  const maxYValue =
    Math.ceil(Math.max(...data.map((item) => item.Overall_Ranked_Score)) / 10) *
    10;

  return (
    <Box
      display="flex"
      justifyContent="center"
      alignItems="center"
      flexDirection="column"
      marginTop={2}
      marginRight={1}
      padding={2}
      border={2}
      borderColor="gray"
      width="98%"
      height="37%"
    >
      <Box width="100%">
        <LineChart
          data={data}
          width={540}
          height={300}
          margin={{ top: 20, right: 40, left: 0, bottom: 20 }}
        >
          <CartesianGrid strokeDasharray="3 3" />
          <XAxis dataKey="name" interval={0} tickCount={data.length} />
          <YAxis
            tickFormatter={formatOverall_Ranked_Score}
            domain={[minYValue, maxYValue]}
          />
          <Tooltip content={<CustomTooltip />} />
          <Legend />
          <Line
            type="monotone"
            dataKey="Overall_Ranked_Score"
            stroke="cyan"
            activeDot={{ r: 8 }}
          />
        </LineChart>
      </Box>
    </Box>
  );
};

export default OsuLineGraph;
