import React, { useEffect, useState } from "react";
import {
  Paper,
  Table,
  TableBody,
  TableCell,
  TableContainer,
  TableHead,
  TableRow,
} from "@mui/material";
import StandardLineGraph from "./StandardLineGraph";
import StandardLineGraph2 from "./StandardLineGraph2";
import StandardImage from "../images/Standard.png";
import "../App.css";

const StandardData = ({ username, apiKey }) => {
  const [userData, setUserData] = useState(null);
  const [standardSSRanks, setStandardSSRanks] = useState(0);
  const [standardPP, setStandardPP] = useState(0);
  const [standardRankedScore, setStandardRankedScore] = useState(0);
  const [standardPlaytime, setStandardPlaytime] = useState(0);
  const [isLoading, setIsLoading] = useState(true);
  const formatNumber = (number) => {
    if (number >= 1000000000000) {
      const trillions = (number / 1000000000000).toFixed(2);
      return `${trillions}T`;
    } else if (number >= 1000000000) {
      const billions = (number / 1000000000).toFixed(2);
      return `${billions}B`;
    } else if (number >= 1000000) {
      const millions = (number / 1000000).toFixed(2);
      return `${millions}M`;
    }
    return number.toLocaleString();
  };

  useEffect(() => {
    const fetchUserData = async () => {
      try {
        const response = await fetch(
          `https://osu.ppy.sh/api/get_user?k=${apiKey}&u=${username}&type=string`,
        );
        const data = await response.json();
        setUserData(data[0]);
        setIsLoading(false);
      } catch (error) {
        console.error("Error fetching user data:", error);
        setIsLoading(false);
      }
    };

    const fetchPlaytime = async (mode) => {
      try {
        const response = await fetch(
          `https://osu.ppy.sh/api/get_user?k=${apiKey}&u=${username}&type=string&m=${mode}`,
        );
        const userData = await response.json();
        if (userData.length > 0) {
          const playtimeInSeconds = parseFloat(
            userData[0].total_seconds_played,
          );
          const playtimeInHours = (playtimeInSeconds / 3600).toFixed(2);
          switch (mode) {
            case 0:
              setStandardPlaytime(playtimeInHours);
              break;
            default:
              break;
          }
        }
      } catch (error) {
        console.error(`Error fetching playtime for mode ${mode}:`, error);
      }
    };

    const fetchSSRanksAndPP = async (mode) => {
      try {
        const response = await fetch(
          `https://osu.ppy.sh/api/get_user?k=${apiKey}&u=${username}&type=string&m=${mode}`,
        );
        const userData = await response.json();
        const ssRanks = parseInt(userData[0].count_rank_ss || 0);
        const sshRanks = parseInt(userData[0].count_rank_ssh || 0);
        const rankedScore = parseInt(userData[0].ranked_score || 0);
        if (mode === 0) {
          setStandardSSRanks(ssRanks + sshRanks);
          setStandardPP(userData[0].pp_raw || 0);
          setStandardRankedScore(rankedScore);
        }
      } catch (error) {
        console.error(`Error fetching user SS ranks in mode ${mode}:`, error);
      }
    };

    fetchUserData();
    fetchSSRanksAndPP(0);
    fetchPlaytime(0);
  }, [username, apiKey]);

  if (isLoading) {
    return <div style={{ marginTop: "20px" }}>Loading...</div>;
  }

  if (!userData) {
    return <div>User not found</div>;
  }

  return (
    <div style={{ display: "flex", marginBottom: "20px" }}>
      <div style={{ flex: 1 }}>
        <TableContainer
          component={Paper}
          style={{
            backgroundColor: "rgba(9, 9, 9, 0.7)",
            border: "2px solid gray",
          }}
        >
          <Table>
            <TableHead>
              <TableRow>
                <TableCell
                  style={{
                    color: "white",
                    fontSize: "26px",
                    padding: "8px",
                    textAlign: "center",
                  }}
                >
                  Mode
                </TableCell>
                <TableCell
                  style={{
                    color: "white",
                    fontSize: "26px",
                    padding: "8px",
                    textAlign: "center",
                  }}
                >
                  Score
                </TableCell>
                <TableCell
                  style={{
                    color: "white",
                    fontSize: "26px",
                    padding: "8px",
                    textAlign: "center",
                  }}
                >
                  SS
                </TableCell>
                <TableCell
                  style={{
                    color: "white",
                    fontSize: "26px",
                    padding: "8px",
                    textAlign: "center",
                  }}
                >
                  PP
                </TableCell>
              </TableRow>
            </TableHead>
            <TableBody>
              <TableRow>
                <TableCell style={{ paddingLeft: "40px" }}>
                  <div className="image-container">
                    <img
                      src={StandardImage}
                      alt="Standard"
                      width="70"
                      className="glow-on-hover"
                    />
                    <div
                      className="playtime-overlay"
                      style={{ fontSize: "18px" }}
                    >
                      {parseInt(standardPlaytime)} Hours
                    </div>
                  </div>
                </TableCell>
                <TableCell
                  style={{
                    color: "white",
                    fontSize: "30px",
                    paddingLeft: "40px",
                  }}
                >
                  {formatNumber(standardRankedScore)}
                </TableCell>
                <TableCell
                  style={{
                    color: "white",
                    fontSize: "30px",
                    paddingLeft: "40px",
                  }}
                >
                  {standardSSRanks}
                </TableCell>
                <TableCell
                  style={{
                    color: "white",
                    fontSize: "30px",
                    paddingLeft: "40px",
                  }}
                >
                  {parseInt(standardPP)}
                </TableCell>
              </TableRow>
            </TableBody>
          </Table>
        </TableContainer>

        <div style={{ flex: 1, marginRight: "20px" }}>
          <StandardLineGraph />
          <StandardLineGraph2 />
        </div>
      </div>
    </div>
  );
};
export default StandardData;
