import React from "react";
import {
  Paper,
  Table,
  TableBody,
  TableCell,
  TableContainer,
  TableRow,
} from "@mui/material";

const Bio = () => {
  const mainStyle = {
    backgroundSize: "cover",
    color: "white",
    padding: "20px",
    minHeight: "100vh",
    display: "flex",
    flexDirection: "column",
    justifyContent: "space-between",
    alignItems: "center",
  };

  const selectModeStyle = {
    position: "fixed",
    bottom: "80px",
    color: "white",
    fontSize: "24px",
  };

  return (
    <div style={mainStyle}>
      <TableContainer
        component={Paper}
        sx={{
          marginTop: "0px",
          width: "575px",
          backgroundColor: "rgba(9, 9, 9, 0.7)",
          border: "2px solid gray",
        }}
      >
        <Table>
          <TableBody>
            <TableRow>
              <TableCell
                sx={{
                  color: "white",
                  fontSize: "30px",
                  borderRight: "1px solid white",
                }}
              >
                Player
              </TableCell>
              <TableCell
                sx={{
                  fontSize: "30px",
                  "& a": {
                    color: "cyan",
                    textDecoration: "none",
                    textDecorationLine: "underline",
                  },
                  "& a:hover": { color: "white" },
                }}
              >
                <a
                  href="https://osu.ppy.sh/users/4075694"
                  target="_blank"
                  rel="noopener noreferrer"
                >
                  yxn
                </a>
              </TableCell>
            </TableRow>
            <TableRow>
              <TableCell
                sx={{
                  color: "white",
                  fontSize: "30px",
                  borderRight: "1px solid white",
                }}
              >
                Monitor
              </TableCell>
              <TableCell
                sx={{
                  fontSize: "30px",
                  display: "flex",
                  alignItems: "center",
                  "& a": {
                    color: "cyan",
                    textDecoration: "none",
                    textDecorationLine: "underline",
                    marginRight: "5px",
                    "&:hover": {
                      color: "white",
                    },
                  },
                }}
              >
                <a
                  href="https://www.amazon.com/ALIENWARE-AW2521HF-24-5-Gaming-Monitor/dp/B087N4LQPN/"
                  target="_blank"
                  rel="noopener noreferrer"
                >
                  AW2521HF
                </a>
                <p style={{ color: "white", margin: "0", marginLeft: "5px" }}>
                  240Hz
                </p>
              </TableCell>
            </TableRow>
            <TableRow>
              <TableCell
                sx={{
                  color: "white",
                  fontSize: "30px",
                  borderRight: "1px solid white",
                }}
              >
                Keyboard
              </TableCell>
              <TableCell
                sx={{
                  fontSize: "30px",
                  "& a": {
                    color: "cyan",
                    textDecoration: "none",
                    textDecorationLine: "underline",
                  },
                  "& a:hover": { color: "white" },
                }}
              >
                <a
                  href="https://www.amazon.com/Logitech-Backlit-Mechanical-Gaming-Keyboard/dp/B01CDYB8AG/"
                  target="_blank"
                  rel="noopener noreferrer"
                >
                  Logitech G610
                </a>
              </TableCell>
            </TableRow>
            <TableRow>
              <TableCell
                sx={{
                  color: "white",
                  fontSize: "30px",
                  borderRight: "1px solid white",
                }}
              >
                Mouse
              </TableCell>
              <TableCell
                sx={{
                  fontSize: "30px",
                  display: "flex",
                  alignItems: "center",
                  "& a": {
                    color: "cyan",
                    textDecoration: "none",
                    textDecorationLine: "underline",
                    marginRight: "5px",
                    "&:hover": {
                      color: "white",
                    },
                  },
                }}
              >
                <a
                  href="https://www.amazon.com/gp/product/B082G5BDNC/"
                  target="_blank"
                  rel="noopener noreferrer"
                >
                  Razer Basilisk v2
                </a>
                <p style={{ color: "white", margin: "0", marginLeft: "5px" }}>
                  1600DPI
                </p>
              </TableCell>
            </TableRow>
            <TableRow>
              <TableCell
                sx={{
                  color: "white",
                  fontSize: "30px",
                  borderRight: "1px solid white",
                }}
              >
                Skin
              </TableCell>
              <TableCell
                sx={{
                  fontSize: "30px",
                  "& a": {
                    color: "cyan",
                    textDecoration: "none",
                    textDecorationLine: "underline",
                  },
                  "& a:hover": { color: "white" },
                }}
              >
                <a
                  href="https://drive.google.com/file/d/15zPvekbNHJCIP7KQ5oA8Ty846sVit7Fg/view?usp=drive_link"
                  target="_blank"
                  rel="noopener noreferrer"
                >
                  Download
                </a>
              </TableCell>
            </TableRow>
          </TableBody>
        </Table>
      </TableContainer>
      <p style={selectModeStyle}>Select a mode</p>
    </div>
  );
};

export default Bio;
