import React, { useEffect, useState } from "react";
import {
  Paper,
  Table,
  TableBody,
  TableCell,
  TableContainer,
  TableHead,
  TableRow,
} from "@mui/material";
import OsuLineGraph from "./OsuLineGraph";
import OsuLineGraph2 from "./OsuLineGraph2";
import OverallImage from "../images/Overall.png";
import "../App.css";

const OverallData = ({ username, apiKey }) => {
  const [userData, setUserData] = useState(null);
  const [standardSSRanks, setStandardSSRanks] = useState(0);
  const [taikoSSRanks, setTaikoSSRanks] = useState(0);
  const [catchTheBeatSSRanks, setCatchTheBeatSSRanks] = useState(0);
  const [osuManiaSSRanks, setOsuManiaSSRanks] = useState(0);
  const [standardPP, setStandardPP] = useState(0);
  const [taikoPP, setTaikoPP] = useState(0);
  const [catchTheBeatPP, setCatchTheBeatPP] = useState(0);
  const [osuManiaPP, setOsuManiaPP] = useState(0);
  const [standardRankedScore, setStandardRankedScore] = useState(0);
  const [taikoRankedScore, setTaikoRankedScore] = useState(0);
  const [catchTheBeatRankedScore, setCatchTheBeatRankedScore] = useState(0);
  const [osuManiaRankedScore, setOsuManiaRankedScore] = useState(0);
  const [standardPlaytime, setStandardPlaytime] = useState(0);
  const [taikoPlaytime, setTaikoPlaytime] = useState(0);
  const [catchTheBeatPlaytime, setCatchTheBeatPlaytime] = useState(0);
  const [osuManiaPlaytime, setOsuManiaPlaytime] = useState(0);
  const [isLoading, setIsLoading] = useState(true);
  const formatNumber = (number) => {
    if (number >= 1000000000000) {
      const trillions = (number / 1000000000000).toFixed(2);
      return `${trillions}T`;
    } else if (number >= 1000000000) {
      const billions = (number / 1000000000).toFixed(2);
      return `${billions}B`;
    } else if (number >= 1000000) {
      const millions = (number / 1000000).toFixed(2);
      return `${millions}M`;
    }
    return number.toLocaleString();
  };

  useEffect(() => {
    const fetchUserData = async () => {
      try {
        const response = await fetch(
          `https://osu.ppy.sh/api/get_user?k=${apiKey}&u=${username}&type=string`,
        );
        const data = await response.json();
        setUserData(data[0]);
        setIsLoading(false);
      } catch (error) {
        console.error("Error fetching user data:", error);
        setIsLoading(false);
      }
    };

    const fetchPlaytime = async (mode) => {
      try {
        const response = await fetch(
          `https://osu.ppy.sh/api/get_user?k=${apiKey}&u=${username}&type=string&m=${mode}`,
        );
        const userData = await response.json();
        if (userData.length > 0) {
          const playtimeInSeconds = parseFloat(
            userData[0].total_seconds_played,
          );
          const playtimeInHours = (playtimeInSeconds / 3600).toFixed(2);
          switch (mode) {
            case 0:
              setStandardPlaytime(playtimeInHours);
              break;
            case 1:
              setTaikoPlaytime(playtimeInHours);
              break;
            case 2:
              setCatchTheBeatPlaytime(playtimeInHours);
              break;
            case 3:
              setOsuManiaPlaytime(playtimeInHours);
              break;
            default:
              break;
          }
        }
      } catch (error) {
        console.error(`Error fetching playtime for mode ${mode}:`, error);
      }
    };

    const fetchSSRanksAndPP = async (mode) => {
      try {
        const response = await fetch(
          `https://osu.ppy.sh/api/get_user?k=${apiKey}&u=${username}&type=string&m=${mode}`,
        );
        const userData = await response.json();
        const ssRanks = parseInt(userData[0].count_rank_ss || 0);
        const sshRanks = parseInt(userData[0].count_rank_ssh || 0);
        const rankedScore = parseInt(userData[0].ranked_score || 0);
        if (mode === 0) {
          setStandardSSRanks(ssRanks + sshRanks);
          setStandardPP(userData[0].pp_raw || 0);
          setStandardRankedScore(rankedScore);
        } else if (mode === 1) {
          setTaikoSSRanks(ssRanks + sshRanks);
          setTaikoPP(userData[0].pp_raw || 0);
          setTaikoRankedScore(rankedScore);
        } else if (mode === 2) {
          setCatchTheBeatSSRanks(ssRanks + sshRanks);
          setCatchTheBeatPP(userData[0].pp_raw || 0);
          setCatchTheBeatRankedScore(rankedScore);
        } else if (mode === 3) {
          setOsuManiaSSRanks(ssRanks + sshRanks);
          setOsuManiaPP(userData[0].pp_raw || 0);
          setOsuManiaRankedScore(rankedScore);
        }
      } catch (error) {
        console.error(`Error fetching user SS ranks in mode ${mode}:`, error);
      }
    };

    fetchUserData();
    fetchSSRanksAndPP(0);
    fetchSSRanksAndPP(1);
    fetchSSRanksAndPP(2);
    fetchSSRanksAndPP(3);
    fetchPlaytime(0);
    fetchPlaytime(1);
    fetchPlaytime(2);
    fetchPlaytime(3);
  }, [username, apiKey]);

  if (isLoading) {
    return <div style={{ marginTop: "20px" }}>Loading...</div>;
  }

  if (!userData) {
    return <div>User not found</div>;
  }

  const totalPP = (
    Number(standardPP) +
    Number(taikoPP) +
    Number(catchTheBeatPP) +
    Number(osuManiaPP)
  ).toFixed(0);

  const totalPlaytime = (
    parseFloat(standardPlaytime) +
    parseFloat(taikoPlaytime) +
    parseFloat(catchTheBeatPlaytime) +
    parseFloat(osuManiaPlaytime)
  ).toFixed(2);

  return (
    <div>
      <div style={{ display: "flex", marginBottom: "20px" }}>
        <div style={{ flex: 1 }}>
          <TableContainer
            component={Paper}
            style={{
              backgroundColor: "rgba(9, 9, 9, 0.7)",
              border: "2px solid gray",
            }}
          >
            <Table>
              <TableHead>
                <TableRow>
                  <TableCell
                    style={{
                      color: "white",
                      fontSize: "26px",
                      padding: "8px",
                      paddingLeft: "20px",
                    }}
                  >
                    Mode
                  </TableCell>
                  <TableCell
                    style={{
                      color: "white",
                      fontSize: "26px",
                      padding: "8px",
                      paddingLeft: "20px",
                    }}
                  >
                    Score
                  </TableCell>
                  <TableCell
                    style={{
                      color: "white",
                      fontSize: "26px",
                      padding: "8px",
                      paddingLeft: "20px",
                    }}
                  >
                    SS
                  </TableCell>
                  <TableCell
                    style={{
                      color: "white",
                      fontSize: "26px",
                      padding: "8px",
                      paddingLeft: "20px",
                    }}
                  >
                    PP
                  </TableCell>
                </TableRow>
              </TableHead>
              <TableBody>
                <TableRow>
                  <TableCell>
                    <div className="image-container">
                      <img
                        src={OverallImage}
                        alt="Total"
                        width="70"
                        className="glow-on-hover"
                      />
                      <div
                        className="playtime-overlay"
                        style={{ fontSize: "18px" }}
                      >
                        {parseInt(totalPlaytime)} Hours
                      </div>
                    </div>
                  </TableCell>
                  <TableCell
                    style={{ color: "white", fontSize: "30px", padding: "4px" }}
                  >
                    {formatNumber(
                      standardRankedScore +
                        taikoRankedScore +
                        catchTheBeatRankedScore +
                        osuManiaRankedScore,
                    )}
                  </TableCell>
                  <TableCell
                    style={{ color: "white", fontSize: "30px", padding: "4px" }}
                  >
                    {standardSSRanks +
                      taikoSSRanks +
                      catchTheBeatSSRanks +
                      osuManiaSSRanks}
                  </TableCell>
                  <TableCell
                    style={{ color: "white", fontSize: "30px", padding: "4px" }}
                  >
                    {totalPP}
                  </TableCell>
                </TableRow>
              </TableBody>
            </Table>
          </TableContainer>
          <div style={{ flex: 1, marginRight: "20px" }}>
            <OsuLineGraph />
            <OsuLineGraph2 />
          </div>
        </div>
      </div>
    </div>
  );
};
export default OverallData;
