import React from "react";
import ReactDOM from "react-dom/client";
import "./index.css";
import App from "./App";
import reportWebVitals from "./reportWebVitals";
import { BrowserRouter, Routes, Route } from "react-router-dom";
import Standard from "./pages/Standard/Standard";
import Overall from "./pages/Overall/Overall";
import Mania from "./pages/Mania/Mania";
import Taiko from "./pages/Taiko/Taiko";
import Catch from "./pages/Catch/Catch";

const root = ReactDOM.createRoot(document.getElementById("root"));

root.render(
  <BrowserRouter>
    <Routes>
      <Route path="/" element={<App />}>
        <Route path="standard" element={<Standard />} />
        <Route path="catch" element={<Catch />} />
        <Route path="taiko" element={<Taiko />} />
        <Route path="mania" element={<Mania />} />
        <Route path="overall" element={<Overall />} />
      </Route>
    </Routes>
  </BrowserRouter>,
);

reportWebVitals();
