import React from "react";
import "./App.css";
import Navbar from "./components/Navbar/Navbar";
import { Outlet, useLocation } from "react-router-dom";
import Bio from "./components/Bio";
import Grid from "@mui/material/Grid";
import SocialMedia from "./components/SocialMedia";

function App() {
  const location = useLocation();

  const mainStyle = {
    backgroundColor: "#111111",
    backgroundImage:
      "url('https://cdn.discordapp.com/attachments/1123759840737636453/1138063612401946644/image.png')",
    backgroundSize: "cover",
    color: "white",
    padding: "20px",
    minHeight: "100vh",
    display: "flex",
    flexDirection: "column",
    alignItems: "center",
    overflowY: "auto",
  };

  const headingStyle = {
    margin: 0,
    backgroundColor: "rgba(9, 9, 9, 0.7)",
    border: "2px solid gray",
    boxSizing: "border-box",
    width: "100%",
  };

  return (
    <div style={mainStyle}>
      <div className="App">
        <header
          className="App-header"
          style={{
            border: "2px solid gray",
            position: "relative",
            backgroundImage:
              "url(https://cdn.discordapp.com/attachments/1123759840737636453/1138022095717142558/dark_monochrome_city.png)",
            backgroundSize: "cover",
            backgroundPosition: "center",
            minHeight: "150px",
          }}
        >
          <div
            style={{
              display: "flex",
              justifyContent: "center",
              alignItems: "center",
              height: "100%",
              position: "relative",
            }}
          >
            <img
              src="https://cdn.discordapp.com/attachments/1123759840737636453/1137617215395483648/yxn_tao2.png"
              className="App-logo"
              alt="logo"
              style={{ zIndex: 2 }}
            />
          </div>
        </header>

        <div style={headingStyle}>
          <SocialMedia />
          <Grid container spacing={0}>
            <Grid item xs={12}>
              <Navbar />
              <Outlet />
              {location.pathname === "/" && <Bio />}
            </Grid>
          </Grid>
        </div>
      </div>
    </div>
  );
}

export default App;
