import React from "react";
import {
  LineChart,
  Line,
  XAxis,
  YAxis,
  CartesianGrid,
  Tooltip,
  Legend,
} from "recharts";
import { Box } from "@mui/material";

const data = [
  { name: "Jan", Catch_Ranked_Score: 64.4 },
  { name: "Feb", Catch_Ranked_Score: 64.4 },
  { name: "Mar", Catch_Ranked_Score: 67.2 },
  { name: "Apr", Catch_Ranked_Score: 70.4 },
  { name: "May", Catch_Ranked_Score: 73.9 },
  { name: "Jun", Catch_Ranked_Score: 76.8 },
  { name: "Jul", Catch_Ranked_Score: 80.2 },
  { name: "Aug", Catch_Ranked_Score: 85.9 },
  { name: "Sep", Catch_Ranked_Score: 85.9 },
  { name: "Oct", Catch_Ranked_Score: 85.9 },
  { name: "Nov", Catch_Ranked_Score: 85.9 },
  { name: "Dec", Catch_Ranked_Score: 85.9 },
];

const formatCatch_Ranked_Score = (Catch_Ranked_Score) => {
  return `${Catch_Ranked_Score}B`;
};

const CatchLineGraph = () => {
  const CustomTooltip = ({ active, payload, label }) => {
    if (active && payload && payload.length) {
      const Catch_Ranked_Score = payload[0].value;
      return (
        <div
          className="custom-tooltip"
          style={{
            backgroundColor: "black",
            color: "white",
            padding: "1px",
            width: "100px",
            border: "1px solid gray",
            transition: "background-color 0.3s",
          }}
        >
          <p className="label">{`${label}: ${formatCatch_Ranked_Score(
            Catch_Ranked_Score,
          )}`}</p>
        </div>
      );
    }
    return null;
  };

  const minYValue = Math.min(...data.map((item) => item.Catch_Ranked_Score));
  const maxYValue =
    Math.ceil(Math.max(...data.map((item) => item.Catch_Ranked_Score)) / 10) *
    10;

  return (
    <Box
      display="flex"
      justifyContent="center"
      alignItems="center"
      flexDirection="column"
      marginTop={2}
      marginRight={1}
      padding={2}
      border={2}
      borderColor="gray"
      width="98%"
      height="37%"
    >
      <Box width="100%">
        <LineChart
          data={data}
          width={540}
          height={300}
          margin={{ top: 20, right: 40, left: 0, bottom: 20 }}
        >
          <CartesianGrid strokeDasharray="3 3" />
          <XAxis dataKey="name" interval={0} tickCount={data.length} />
          <YAxis
            tickFormatter={formatCatch_Ranked_Score}
            domain={[minYValue, maxYValue]}
          />
          <Tooltip content={<CustomTooltip />} />
          <Legend />
          <Line
            type="monotone"
            dataKey="Catch_Ranked_Score"
            stroke="cyan"
            activeDot={{ r: 8 }}
          />
        </LineChart>
      </Box>
    </Box>
  );
};

export default CatchLineGraph;
